<template>
  <div class="follow-operation">
    <el-tabs
      v-model="activeName"
      style="width: 100%"
      @tab-click="getSourceList(channelSearch)"
    >
      <el-tab-pane label="企业微信二维码" name="code" />
      <el-tab-pane label="小程序按钮" name="btn" />
    </el-tabs>
    <!--  搜索  -->
    <el-form
      ref="channelSearch"
      label-position="left"
      :model="channelSearch"
      inline
    >
      <el-form-item label="关键词" class="search-item">
        <el-input
          v-model="channelSearch.keyword"
          clearable
          placeholder="请输入关键词"
        />
      </el-form-item>
      <el-form-item label="来源渠道" class="search-item">
        <el-select
          v-model="channelSearch.source"
          clearable
          placeholder="请选择来源渠道"
        >
          <el-option
            v-for="item in sourceList"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="类型" class="search-item">
        <el-select
          v-model="channelSearch.type"
          clearable
          placeholder="请选择类型"
        >
          <el-option
            v-for="item in typeList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item style="float: right">
        <el-button type="primary" class="select-btn" @click="onSubmit"
          >查询</el-button
        >
        <el-button type="primary" class="select-btn" @click="resetForm"
          >重置</el-button
        >
      </el-form-item>
    </el-form>
    <!--  列表  -->
    <div class="list">
      <div class="title">
        <el-button
          type="primary"
          @click="
            $router.push({
              name: 'addSource',
              query: { mode: activeName === 'code' ? 2 : 1 }
            })
          "
          >新增场景</el-button
        >
      </div>
      <el-table
        :data="list"
        style="width: 100%"
        border
        :empty-text="hasListInfo"
      >
        <el-table-column
          fixed
          prop="num"
          label="序号"
          width="100"
          align="center"
        />
        <el-table-column prop="name" label="场景名称" align="center" />
        <el-table-column prop="source" label="匹配渠道" align="center" />
        <el-table-column prop="type" label="类型" align="center" />
        <el-table-column prop="personnel" label="使用人员" align="center" />
        <el-table-column
          prop="verification"
          label="是否需要验证"
          align="center"
        />
        <el-table-column prop="created_at" label="创建时间" align="center" />
        <el-table-column fixed="right" label="操作" width="200" align="center">
          <template slot-scope="scope">
            <el-button
              v-if="activeName === 'code'"
              type="text"
              size="small"
              @click="getQRCode(scope.row.id, scope.row.name)"
              >下载
            </el-button>
            <el-button
              v-if="activeName === 'btn'"
              type="text"
              size="small"
              @click="
                rowInfo = scope.row
                useShow = true
              "
              >使用指引</el-button
            >
            <el-button
              type="text"
              size="small"
              @click="
                $router.push({
                  name: 'editSource',
                  params: { id: scope.row.id },
                  query: { mode: activeName === 'code' ? 2 : 1 }
                })
              "
              >编辑</el-button
            >
            <el-button
              type="text"
              size="small"
              @click="
                rowInfo = scope.row
                delShow = true
              "
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          :current-page="currentPage"
          :page-sizes="[10, 20, 50]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="count"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>
    <el-dialog
      title="删除提示"
      :visible.sync="delShow"
      width="400px"
      center
      top="30vh"
    >
      <p class="center">
        删除后，该企业微信二维码将不存在，
        <br />请确定是否删除？
      </p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delConfirm">确 定</el-button>
        <el-button type="primary" @click="delShow = false">取 消</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="使用指引"
      :visible.sync="useShow"
      width="500px"
      center
      top="30vh"
      class="use-show"
    >
      <div class="center">
        <img
          v-if="rowInfo.style === 1"
          class="btn-style"
          :src="styleOne"
          alt="样式一"
        />
        <img
          v-if="rowInfo.style === 2"
          class="btn-style"
          :src="styleTwo"
          alt="样式二"
        />
        <img
          v-if="rowInfo.style === 3"
          class="btn-style"
          :src="styleThree"
          alt="样式三"
        />
        <p style="margin-top: 5px">{{ rowInfo.name }} {{ rowInfo.source }}</p>
        <p class="style-item">
          <span>搜索ID：{{ rowInfo.id }}</span>
          <el-button type="primary" size="mini" @click="handleCopy(rowInfo.id)"
            >复制</el-button
          >
        </p>
        <p class="style-item">
          <span>配置ID：{{ rowInfo.config_id }}</span>
          <el-button
            type="primary"
            size="mini"
            @click="handleCopy(rowInfo.config_id)"
            >复制</el-button
          >
        </p>
      </div>
      <span slot="footer"> </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  getSourceList,
  deleteSource,
  getUserType,
  getQrcode
} from '@/api/assistantManage'
import styleOne from '@/assets/images/style-one.png'
import styleTwo from '@/assets/images/style-two.png'
import styleThree from '@/assets/images/style-three.png'
export default {
  data() {
    return {
      isOpen: false,
      list: [],
      currentPage: 1,
      delShow: false,
      useShow: false,
      followTitle: '',
      btnText: '',
      followShow: false,
      pageSize: 10,
      count: 0,
      rowInfo: '',
      styleOne: styleOne,
      styleTwo: styleTwo,
      styleThree: styleThree,
      copyData: null,
      hasListInfo: '暂无数据',
      singleList: [],
      activeName: 'code',
      createNameList: [],
      sourceList: [],
      channelSearch: {
        type: '',
        keyword: '',
        source: ''
      },
      typeList: [
        { value: 1, label: '单人' },
        { value: 2, label: '多人' }
      ]
    }
  },
  computed: {
    getSourceChannel() {
      return function(obj) {
        if (obj) {
          return `${obj.first_name}-${obj.second_name}${
            obj.three_name ? '-' + obj.three_name : ''
          }`
        }
        return '/'
      }
    },
    getPersonnel() {
      return function(user) {
        let person = ''
        for (const item of user) {
          person += item.name + ' '
        }
        return person
      }
    }
  },
  created() {
    this.getSourceList()
    this.getUserType()
  },
  methods: {
    onSubmit() {
      this.currentPage = 1
      this.getSourceList(this.channelSearch)
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getSourceList(this.channelSearch)
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getSourceList(this.channelSearch)
    },
    resetForm() {
      this.channelSearch = {
        type: '',
        keyword: '',
        source: ''
      }
      this.getSourceList()
    },
    handleCopy(id) {
      this.copyData = id
      this.copy(this.copyData)
    },
    copy(data) {
      let url = data
      let oInput = document.createElement('input')
      oInput.value = url
      document.body.appendChild(oInput)
      oInput.select() // 选择对象;
      console.log(oInput.value)
      document.execCommand('Copy') // 执行浏览器复制命令
      this.$message({
        message: '复制成功',
        type: 'success'
      })
      oInput.remove()
    },
    async delConfirm() {
      const { code, message } = await deleteSource(this.rowInfo.id)
      if (code === 10000) {
        this.$message.success(message)
        this.getSourceList(this.channelSearch)
        this.delShow = false
      }
    },
    async getQRCode(id, name) {
      /* eslint-disable no-new */
      const { code, data } = await getQrcode(id)
      if (code === 10000) {
        var img = new Image()
        img.setAttribute('crossOrigin', 'Anonymous')
        img.onload = function() {
          var canvas = document.createElement('canvas')
          canvas.width = img.width
          canvas.height = img.height
          var ctx = canvas.getContext('2d')
          // 将img中的内容画到画布上
          ctx.drawImage(img, 0, 0, canvas.width, canvas.height)
          // 将画布内容转换为base64
          var base64 = canvas.toDataURL()
          // 创建a链接
          var a = document.createElement('a')
          a.href = base64
          a.download = name
          // 触发a链接点击事件，浏览器开始下载文件
          a.click()
        }
        img.src = data.contact_way.qr_code
        // 必须设置，否则canvas中的内容无法转换为blob
      }
    },
    inputFormSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.rowInfo.num === 1) this.createFollow()
          if (this.rowInfo.num === 2) this.updateFollow()
        } else {
          return false
        }
      })
    },
    async getSourceList(search) {
      let params = {
        pageSize: this.pageSize,
        page: this.currentPage,
        mode: this.activeName === 'code' ? 2 : 1
      }
      if (search) {
        if (search.type) params.type = search.type
        if (search.keyword) params.key_word = search.keyword
        if (search.source) params.source_channel = search.source
        this.hasListInfo = '暂无数据，请新增场景'
      } else {
        this.hasListInfo = '暂无数据'
      }
      const { data, code } = await getSourceList(params)
      if (code === 10000) {
        this.count = data.count
        this.list = data.list.map((item, index) => {
          return {
            num: (this.currentPage - 1) * this.pageSize + index + 1,
            id: item._id,
            name: item.name,
            type: item.type === 1 ? '单人' : '多人',
            personnel: this.getPersonnel(item.user) || '/',
            source: this.getSourceChannel(item.source_channel),
            created_at: item.created_at
              ? this.formatTime(item.created_at)
              : '-',
            verification: item.skip_verify ? '否' : '是',
            config_id: item.config_id,
            style: item.style
          }
        })
      }
    },
    async getUserType() {
      const { code, data } = await getUserType()
      if (code === 10000) {
        this.sourceList = data.list.map(v => {
          return {
            name: `${v.first_name}-${v.second_name}${
              v.three_name ? '-' + v.three_name : ''
            }`,
            id: v._id
          }
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.follow-operation {
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;
  margin-bottom: 20px;
  /deep/form {
    width: 100%;
    .el-date-editor {
      padding: 0 10px;
      width: 220px;
      height: 32px;
      .el-range-input {
        width: 85px;
        height: 30px;
      }
    }
  }
  .search-item {
    color: #17233d;
    &:not(:nth-child(4)) {
      margin-right: 50px;
    }
    /deep/input {
      width: 220px;
      height: 32px;
      line-height: 32px;
    }
    /deep/span {
      top: -3px;
    }
    /deep/.el-input__suffix {
      top: 0;
    }
  }
  .select-btn {
    padding: 0 20px;
    width: 68px;
    height: 32px;
  }
  .arrow {
    float: right;
  }
  .list {
    margin: 20px 0 0;
    width: 100%;
    .title {
      line-height: 32px;
      text-align: right;
      /deep/button {
        margin-bottom: 10px;
        padding: 6px 20px;
        height: 32px;
      }
    }
    /deep/.el-dialog__wrapper {
      button {
        padding: 6px 20px;
        height: 32px;
      }
      .el-dialog__headerbtn {
        top: 10px;
        right: 5px;
      }
    }
    .pagination {
      margin-top: 50px;
      text-align: center;
    }
  }
  /deep/.cell {
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
    *:not(button) {
      display: inline-block;
      width: 100%;
    }
  }
  .center {
    line-height: 20px;
    text-align: center;
  }
  .button-wrap {
    button {
      height: 32px;
      line-height: 0;
    }
  }
  .danger-tip {
    margin-top: 10px;
    line-height: 16px;
    color: #f00;
  }
  .el-dialog__wrapper {
    /deep/.el-input__inner {
      width: 220px;
    }
  }
  .demo-ruleForm {
    /deep/.el-input {
      width: 220px;
    }
  }
  .style-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 5px;
    padding: 0 40px;
    line-height: 40px;
  }
  .use-show {
    /deep/.el-dialog__footer {
      padding: 0;
    }
  }
  .btn-style {
    border: 1px solid rgba(228, 230, 233, 1);
    border-radius: 4px;
  }
  /deep/.el-button--mini {
    padding: 2px 5px;
    width: 60px;
    height: 22px;
  }
}
</style>
